<template>
  <div class="container">
    <div class="row">
      <div class="col-12 mb-2">
        <h3>Comisiones de Empleados</h3>
      </div>
    </div>
    <div class="row">
      <div class="offset-7 col-1 text-right p-0 mt-2">
        <label for="fecha" class="font-25"> Dia: </label>
      </div>
      <div class="col-4 text-center">
        <datepicker
          id="fecha"
          input-class="form-control"
          format="dd/MMM/yyyy"
          v-model="fechaComisionSeleccionada"
          calendar-button-icon="fa fa-calendar"
          :language="language"
          :calendar-button="true"
          :bootstrap-styling="true"
          :use-utc="true"
        >
        </datepicker>
      </div>
    </div>
    <div class="row mt-3" v-if="empleados.length > 0">
      <div class="col-12">
        <table class="table table-striped table-sm " id="tablaClientes">
          <thead class="table-primary">
            <th class="col-2">Código</th>
            <th class="col-5">Nombre</th>
            <th class="col-3">Celular</th>
            <th class="col-2 text-center">Comisiones</th>
          </thead>
          <tbody ref="tablaClientesBody">
            <tr :key="index" :id="'row' + index" v-for="(item, index) in empleados">
              <td v-text="item.codigo"></td>
              <td v-text="item.nombreCompleto"></td>
              <td v-text="item.celular"></td>
              <td class="text-center">
                <button
                  class="btn btn-sm btn-secondary"
                  @click="DetalleEmpleadoComison(item)"
                >
                  Mostrar
                </button>
              </td>
            </tr>
          </tbody>
        </table>
      </div>
    </div>
    <div v-else class="mt-3">
      <div class="alert alert-warning text-center pt-1 pb-0" role="alert">
        <h5>No hay empleados con comisiones asignados.</h5>
      </div>
    </div>
    <template>
      <div>
        <b-modal
          hide-footer
          centered
          :title="tituloModal"
          v-model="mostrarComisiones"
          class="font-8"
          size="lg"
          no-close-on-backdrop
        >
          <div class="container">
            <div class="row">
              <div class="col-12 mb-3">
                <b-form-checkbox @change="AsignaMontoRecomendado" v-if="!montoAsignado">
                  Asignar monto recomendado
                </b-form-checkbox>
              </div>
              <div class="col-12" v-if="montoAsignado">
                <table class="table table-striped table-sm" id="tablaClientes">
                  <thead class="table-primary">
                    <th class="col-6">Descripcion</th>
                    <th class="col-3 text-center">Monto</th>
                  </thead>
                  <tbody ref="tablaClientesBody">
                    <tr
                      :key="index"
                      :id="'row' + index"
                      v-for="(item, index) in comisionesEmpleado"
                    >
                      <td v-text="item.descripcion"></td>
                      <td>
                        <input
                          onkeypress="return event.charCode >= 48 && event.charCode <= 57"
                          class="form-control"
                          disabled
                          v-model="item.monto"
                          style="text-align: right"
                        />
                      </td>
                    </tr>
                  </tbody>
                </table>
              </div>
              <div class="col-12" v-else>
                <table class="table table-stripped table-sm" id="tablaClientes">
                  <thead>
                    <th class="col-6">Descripcion</th>
                    <th class="col-3 text-center">Monto Asignado</th>
                    <th class="col-3 text-center">Monto Recomendado</th>
                  </thead>
                  <tbody ref="tablaClientesBody">
                    <tr
                      :key="index"
                      :id="'row' + index"
                      v-for="(item, index) in comisionesEmpleado"
                    >
                      <td v-text="item.descripcion"></td>
                      <td>
                        <input
                          onkeypress="return event.charCode >= 48 && event.charCode <= 57"
                          v-selectonfocus
                          class="form-control"
                          v-bind:disabled="isDisabled == 1"
                          v-model="item.monto"
                          style="text-align: right"
                        />
                      </td>
                      <td>
                        <input
                          onkeypress="return event.charCode >= 48 && event.charCode <= 57"
                          v-selectonfocus
                          class="form-control"
                          v-model="item.cantidadMaximaComision"
                          disabled
                          style="text-align: right"
                        />
                      </td>
                    </tr>
                  </tbody>
                </table>
              </div>
            </div>
            <div class="row">
              <div class="col-12 text-right">
                <button
                  class="btn btn-sm btn-primary col-3"
                  @click="AceptarComisionEmpleado"
                >
                  <i class="fas fa-save"></i> {{ btnModal }}
                </button>
              </div>
            </div>
          </div>
        </b-modal>
      </div>
    </template>
  </div>
</template>
<script>
import servicioComisiones from "@/services/servicio-comisiones";
import Datepicker from "vuejs-datepicker";
import { es } from "vuejs-datepicker/dist/locale";
export default {
  data() {
    return {
      language: es,
      calendario: true,
      btnModal: "Guardar",
      comisionSeleccionada: false,
      fechaComisionSeleccionada: new Date(
        new Date().getFullYear(),
        new Date().getMonth(),
        new Date().getDate()
      ),
      mostrarComisiones: false,
      montoAsignado: false,
      isDisabled: 0,
      tituloModal: "",
      empleados: [],
      empleadoSeleccionado: [],
      comisionesEmpleado: [],
      comisiones: [],
      comisionesSeleccionadas: [],
    };
  },
  async mounted() {
    await this.ObtenerEmpleadosConComision();
  },
  components: {
    Datepicker,
  },
  computed: {},
  watch: {},
  methods: {
    AsignaMontoRecomendado() {
      this.comisionesEmpleado.forEach((element, index) => {
        this.comisionesEmpleado[index].monto = element.cantidadMaximaComision;
      });
    },
    async ObtenerEmpleadosConComision() {
      this.$loading(true);
      let r = await servicioComisiones.empleados();
      this.$loading(false);
      if (r.value) {
        this.empleados = r.data;
      }
    },
    async DetalleEmpleadoComison(empleado) {
      this.empleadoSeleccionado = empleado;
      this.tituloModal = `Comisiones asignadas a ${empleado.nombreCompleto}`;
      this.$loading(true);
      let r = await servicioComisiones.comisionesPorEmpleado(
        empleado.codigo,
        this.fechaComisionSeleccionada
      );
      this.$loading(false);
      if (r.value) {
        console.log(r);
        if (r.data.length > 0) {
          this.btnModal = "Modificar";
          this.comisionesEmpleado = r.data;
          this.mostrarComisiones = true;
          this.montoAsignado = true;
        }
      } else {
        this.montoAsignado = false;
        this.comisionesEmpleado = r.data;
        this.mostrarComisiones = true;
      }
    },
    dateFormat(date, options) {
      return Intl.DateTimeFormat("es-MX", options).format(date);
    },
    async AceptarComisionEmpleado() {
      if (!this.montoAsignado) {
        let montoCorrecto = this.VerificarMonto();
        if (montoCorrecto) {
          await this.guardarComisiones();
        }
      }else{
        console.log("asd");
      }
    },
    async guardarComisiones() {
      let msgRes = await this.$swal.fire({
        title: "¡ATENCIÓN!",
        text: `¿Está seguro de guardar el monto de las comisiones? `,
        showCancelButton: true,
        icon: "question",
        confirmButtonColor: "#3085d6",
        cancelButtonColor: "#d33",
        confirmButtonText: "Aceptar",
        cancelButtonText: "Cancelar",
      });

      if (msgRes.value) {
        let datos = {
          comisiones: this.comisionesEmpleado,
          codEmpleado: this.empleadoSeleccionado.codigo,
          fechaComision: this.fechaComisionSeleccionada,
        };
        let r = await servicioComisiones.guardarComisiones(datos);

        if (r.value) {
          this.mostrarComisiones = false;
          this.$swal.fire({
            text: r.message,
            icon: "info",
          });
        } else {
          this.$swal.fire({
            text: r.message,
            icon: "info",
          });
        }
      }
    },
    VerificarMonto() {
      for (let index = 0; index < this.comisionesEmpleado.length; index++) {
        let monto = this.comisionesEmpleado[index].monto;
        let recomendado = this.comisionesEmpleado[index].cantidadMaximaComision;

        if (monto > recomendado) {
          this.$swal.fire({
            text: `El monto asignado de $${monto} no debe ser mayor a la cantidad de $${recomendado} que se le recomienda`,
            icon: "info",
          });
          this.comisionesEmpleado[index].monto = 0;
          return false;
        }
      }
      return true;
    },
  },
};
</script>
<style scoped></style>
